

































































































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CStatistics from '@/components/links/CStatistics.vue';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'

@Component({
  components: {
    CStatistics,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '相関関係にある正規乱数',
    },
  },
})
export default class CorrelationRandom extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataKeyRegExps: {
    sample: RegExp,
  } = {
    sample: /\{ *sample\ *\}/g,
  };
  private dataFormat: string = '';
  private dataSelectedFormatExampleIndex: number = 0;
  private dataFormatExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'comma',
      caption: 'カンマ区切り',
      content: '{ sample },',
    },
    {
      name: 'new_line',
      caption: '改行区切り',
      content: '{ sample }\n',
    },
    {
      name: 'semicolon',
      caption: 'セミコロン区切り',
      content: '{ sample }:',
    },
    {
      name: 'slash',
      caption: 'スペース区切り',
      content: '{ sample } ',
    },
  ];
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: ',,,,,,,',
    },
    {
      name: 'standard_normal_distribution',
      caption: '正の強い相関関係にある標準正規分布',
      content: '1000,0.8,0,1,4,0,1,4',
    },
    {
      name: 'standard_normal_distribution',
      caption: '負の弱い相関関係にある標準正規分布',
      content: '1000,-0.2,0,1,4,0,1,4',
    },
    {
      name: 'height_male',
      caption: '25-29歳男子身長と体重',
      content: '1000,0.6,172.11,5.69,2,67.26,9.11,2',
    },
    {
      name: 'test',
      caption: 'センター試験（国語と英語）',
      content: '1000,0.8,111.29,33.10,0,122.78,41.24,0',
    },
    {
      name: 'test2',
      caption: 'センター試験（国語と数学II）',
      content: '1000,0.5,111.29,33.10,0,31.73,18.29,0',
    },
  ];
  private dataSampleSize: string = '';
  private dataCorrelationCoefficient: string = '';
  private dataMeanX: string = '';
  private dataMeanY: string = '';
  private dataStandardDiviationX: string = '';
  private dataStandardDiviationY: string = '';
  private dataDecimalPlaceX: string = '';
  private dataDecimalPlaceY: string = '';
  private dataTextX: string = '';
  private dataTextY: string = '';

  private created(): void {
  }

  // Methods
  private listenCorrelationCoefficientInput($event: Event): void {
    let number: string = (<HTMLInputElement>(<any>$event).target).value;
    this.dataCorrelationCoefficient = number;
    if (!MathUtil.isRealNumber(number)
      || Number(number) < -1
      || 1 < Number(number)
    ) {
      this.dataCorrelationCoefficient = '';
    }
  }
  private listenSampleSizeInput($event: Event): void {
    let number: string = (<HTMLInputElement>(<any>$event).target).value;
    this.dataSampleSize = number;
    if (!MathUtil.isNaturalNumberIncludingZero(number) || Number(number) === 0) {
      this.dataSampleSize = '';
    }
  }
  private listenMeanXInput($event: Event): void {
    this.dataMeanX = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isRealNumber(this.dataMeanX)) {
      this.dataMeanX = '';
    }
  }
  private listenMeanYInput($event: Event): void {
    this.dataMeanY = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isRealNumber(this.dataMeanY)) {
      this.dataMeanY = '';
    }
  }
  private listenStandardDiviationXInput($event: Event): void {
    this.dataStandardDiviationX = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isRealNumber(this.dataStandardDiviationX)) {
      this.dataStandardDiviationX = '';
    }
  }
  private listenStandardDiviationYInput($event: Event): void {
    this.dataStandardDiviationY = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isRealNumber(this.dataStandardDiviationY)) {
      this.dataStandardDiviationY = '';
    }
  }
  private listenDecimalPlaceXInput($event: Event): void {
    this.dataDecimalPlaceX = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isNaturalNumberIncludingZero(this.dataDecimalPlaceX)) {
      this.dataDecimalPlaceX = '';
    }
  }
  private listenDecimalPlaceYInput($event: Event): void {
    this.dataDecimalPlaceY = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isNaturalNumberIncludingZero(this.dataDecimalPlaceY)) {
      this.dataDecimalPlaceY = '';
    }
  }
  private listenInput($event: Event): void {
    this.dataFormat = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnFormatExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    this.dataFormat = this.dataFormatExamples[index].content;
    this.dataSelectedFormatExampleIndex = index;
  }
  private listenOnSampleExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    let samples: string[] = this.dataSampleExamples[index].content.split(',');
    this.dataSampleSize = samples[0];
    this.dataCorrelationCoefficient = samples[1];
    this.dataMeanX = samples[2];
    this.dataStandardDiviationX = samples[3];
    this.dataDecimalPlaceX = samples[4];
    this.dataMeanY = samples[5];
    this.dataStandardDiviationY = samples[6];
    this.dataDecimalPlaceY = samples[7];
    this.dataSelectedSampleExampleIndex = index;
  }
  private outputText(): void {
    this.dataTextX = '';
    this.dataTextY = '';
    if (this.dataFormat !== ''
      && this.dataCorrelationCoefficient !== ''
      && this.dataSampleSize !== '' 
      && this.dataMeanX !== '' 
      && this.dataStandardDiviationX !== ''
      && this.dataDecimalPlaceX !== ''
      && this.dataMeanY !== '' 
      && this.dataStandardDiviationY !== ''
      && this.dataDecimalPlaceY !== ''
    ) {
      let sampleXs: number[] = StatisticsCalc.generateSamples(Number(this.dataSampleSize), 0, 1);
      let sampleYs: number[] = StatisticsCalc.generateCorrelationalSamples(sampleXs, Number(this.dataCorrelationCoefficient));
      sampleXs = StatisticsCalc.unstandardizeSamples(sampleXs, Number(this.dataMeanX), Number(this.dataStandardDiviationX));
      sampleYs = StatisticsCalc.unstandardizeSamples(sampleYs, Number(this.dataMeanY), Number(this.dataStandardDiviationY));
      this.dataTextX = this.formatSamples(sampleXs, Number(this.dataDecimalPlaceX));
      this.dataTextY = this.formatSamples(sampleYs, Number(this.dataDecimalPlaceY));
    }
  }
  private formatSamples(normalRandoms: number[], decimalPlace: number): string {
    let text: string = '';
    for (let i: number = 0; i < normalRandoms.length; i++) {
      let normalRandomText: string = this.dataFormat;
      normalRandomText = normalRandomText.replace(
        this.dataKeyRegExps.sample,
        MathUtil.toFixed(Number(normalRandoms[i]), decimalPlace)
      );
      text += normalRandomText;
    }
    return text;
  }
}
